const utils = require('./utils.js');

$(document).ready(function () {
  var options = {
    valueNames: [ 'wrestler-name', 'trainer-buff', 'trainer-type' ],
    page: 10,
    pagination: [{
      paginationClass: "paginationTop",
      innerWindow: 1
    }, {
      paginationClass: "paginationBottom",
      innerWindow: 1
    }]
  };
  var trainersList = new List('trainers', options);

  $('a.filter-by-type').on("click", function(event){
    var filter = $(this).attr("data-type-id");
    $('button.filter-by-type-button').html($(this).text() + " <span class='caret'></span>");
    if (filter == "All") {
      trainersList.filter();
    } else {
      trainersList.filter(function(item){
        if (item.values()["trainer-type"] == filter) {
          return true;
        } else {
          return false;
        }
      });
    }
    event.preventDefault();
  });


  function attachLevelRangeEvent() {
    $('input.trainer-level').on('input', function() {
      var curLevel = parseInt($(this).val());
      var maxLevel = $(this).attr('max');
      //console.log(curLevel + " / " + maxLevel);
  
      var $trainer = $($(this).closest('.trainer').first());
      var trainerLevelData = $trainer.find("input.trainer-level-data[data-level=" + curLevel + "]").first();
      var trainerBuff = $trainer.find('.trainer-buff').first();
      var currentTalentScore = $trainer.find('.trainer-current-talent-score').first();
      var currentLevelNumber = $trainer.find('.trainer-current-level-number').first();
  
      trainerBuff.empty();
      trainerBuff.text(trainerLevelData.val());
  
      currentTalentScore.empty();
      currentTalentScore.text(trainerLevelData.attr("data-talent-score"));
  
      currentLevelNumber.empty();
      currentLevelNumber.text(trainerLevelData.attr("data-level"));
    });
  }

  attachLevelRangeEvent();
  trainersList.on('updated', function() {
    attachLevelRangeEvent();
  });
});



